"use client";

import { useState } from "react";
import Link from "next/link";
import FooterBar from "@/components/FooterBar";

export default function ChatPage() {
  const [messages, setMessages] = useState([
    {
      id: 1,
      text: "你好！我是你的天文助手，有什么可以帮助你的吗？",
      isUser: false,
      time: "14:30",
    },
    { id: 2, text: "我想了解今天的月相", isUser: true, time: "14:31" },
    {
      id: 3,
      text: "今天是农历六月廿九，月相为下弦月，照明度约99.6%",
      isUser: false,
      time: "14:31",
    },
  ]);
  const [inputText, setInputText] = useState("");

  const sendMessage = () => {
    if (inputText.trim()) {
      const newMessage = {
        id: messages.length + 1,
        text: inputText,
        isUser: true,
        time: new Date().toLocaleTimeString("zh-CN", {
          hour: "2-digit",
          minute: "2-digit",
        }),
      };
      setMessages([...messages, newMessage]);
      setInputText("");

      // 模拟AI回复
      setTimeout(() => {
        setMessages((prev) => [
          ...prev,
          {
            id: prev.length + 1,
            text: "我收到了你的消息，正在处理中...",
            isUser: false,
            time: new Date().toLocaleTimeString("zh-CN", {
              hour: "2-digit",
              minute: "2-digit",
            }),
          },
        ]);
      }, 1000);
    }
  };

  return (
    <div className="bg-black text-white min-h-screen flex flex-col">
      {/* 背景星空效果 */}
      <div className="fixed inset-0 bg-black">
        <div className="absolute inset-0 opacity-30">
          {[...Array(50)].map((_, i) => (
            <div
              key={i}
              className={`absolute w-1 h-1 bg-white rounded-full star`}
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`,
              }}
            ></div>
          ))}
        </div>
      </div>

      <div className="relative z-10 flex flex-col h-screen">
        {/* 顶部导航 */}
        <div className="flex justify-between items-center p-4 border-b border-gray-700">
          <Link
            href="/wel"
            className="flex items-center space-x-2 text-blue-400 hover:text-blue-300"
          >
            <div className="w-4 h-4 border-l-2 border-t-2 border-current transform rotate-[-45deg]"></div>
            <span>返回</span>
          </Link>
          <h1 className="text-xl font-semibold">聊天助手</h1>
          <div className="w-8"></div>
        </div>

        {/* 聊天消息区域 */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${
                message.isUser ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                  message.isUser
                    ? "bg-blue-500 text-white"
                    : "bg-gray-700 text-white"
                }`}
              >
                <p className="text-sm">{message.text}</p>
                <p
                  className={`text-xs mt-1 ${
                    message.isUser ? "text-blue-100" : "text-gray-400"
                  }`}
                >
                  {message.time}
                </p>
              </div>
            </div>
          ))}
        </div>

        {/* 输入区域 */}
        <div className="p-4 border-t border-gray-700">
          <div className="flex space-x-2">
            <input
              type="text"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              onKeyPress={(e) => e.key === "Enter" && sendMessage()}
              placeholder="输入你的问题..."
              className="flex-1 bg-gray-800 text-white px-4 py-2 rounded-lg border border-gray-600 focus:outline-none focus:border-blue-500"
            />
            <button
              onClick={sendMessage}
              className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition-colors"
            >
              发送
            </button>
          </div>
        </div>

        {/* 底部导航栏 */}
        <FooterBar />
      </div>
    </div>
  );
}
